"use client";

import React, { useState } from "react";
import { useResumeStore } from "@/store/useResumeStore";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Button } from "@/components/ui/button";
import { Card, CardContent } from "@/components/ui/card";
import { Plus, Sparkles, Loader2, GraduationCap, Briefcase, Target, Download } from "lucide-react";
import jsPDF from "jspdf";
import html2canvas from "html2canvas";

export default function ResumeBuilder() {
  const { resume, setPersonalInfo, addEducation, updateEducation, addExperience, updateExperience, setJobDescription } = useResumeStore() as any;
  const [loadingIndex, setLoadingIndex] = useState<number | null>(null);
  const [matchScore, setMatchScore] = useState<number | null>(null);

  // PDF 下载功能
  const downloadPDF = async () => {
    const element = document.getElementById("resume-preview");
    if (!element) return;
    const canvas = await html2canvas(element, { scale: 2 });
    const imgData = canvas.toDataURL("image/png");
    const pdf = new jsPDF("p", "mm", "a4");
    pdf.addImage(imgData, "PNG", 0, 0, 210, 297);
    pdf.save("PathResume_US_Tech.pdf");
  };

  // AI SmartMatch 功能
  const runSmartMatch = async () => {
    const res = await fetch("/api/optimize", {
      method: "POST",
      body: JSON.stringify({ type: 'MATCH', jd: resume.jobDescription }),
    });
    const data = await res.json();
    setMatchScore(data.score);
  };

  // AI 润色功能
  const handleOptimize = async (index: number, text: string) => {
    setLoadingIndex(index);
    const res = await fetch("/api/optimize", {
      method: "POST",
      body: JSON.stringify({ text }),
    });
    const data = await res.json();
    updateExperience(index, "description", data.result);
    setLoadingIndex(null);
  };

  return (
    <div className="flex h-screen bg-slate-50 text-slate-900">
      {/* 左侧编辑器 */}
      <div className="w-1/2 p-8 overflow-y-auto border-r border-slate-200">
        <div className="max-w-xl mx-auto space-y-10 pb-20">
          <header className="flex justify-between items-center">
            <h1 className="text-3xl font-black text-blue-600">PathResume <span className="text-slate-400 font-light text-sm italic">v1.0 Pro</span></h1>
            <Button onClick={downloadPDF} className="bg-black text-white hover:bg-slate-800">
              <Download className="w-4 h-4 mr-2" /> Export PDF
            </Button>
          </header>

          {/* AI SmartMatch 面板 */}
          <Card className="bg-blue-50 border-blue-100 shadow-sm">
            <CardContent className="pt-6 space-y-4">
              <div className="flex items-center justify-between">
                <h2 className="text-sm font-bold flex items-center gap-2"><Target className="w-4 h-4" /> AI SmartMatch™ (ATS Score)</h2>
                {matchScore && <span className="text-2xl font-black text-blue-700">{matchScore}%</span>}
              </div>
              <textarea 
                placeholder="Paste Job Description (JD) here to check compatibility..."
                className="w-full p-3 text-xs border rounded-md h-24"
                onChange={(e) => setJobDescription(e.target.value)}
              />
              <Button onClick={runSmartMatch} size="sm" className="w-full bg-blue-600">Analyze Compatibility</Button>
            </CardContent>
          </Card>

          {/* 个人信息 */}
          <div className="space-y-4">
            <h2 className="font-bold flex items-center gap-2"><Plus className="w-4 h-4" /> Contact Information</h2>
            <div className="grid grid-cols-2 gap-4">
              <Input placeholder="Full Name" value={resume.personalInfo.fullName} onChange={(e) => setPersonalInfo({...resume.personalInfo, fullName: e.target.value})} />
              <Input placeholder="Email (Professional)" value={resume.personalInfo.email} onChange={(e) => setPersonalInfo({...resume.personalInfo, email: e.target.value})} />
            </div>
          </div>

          {/* 教育背景 */}
          <div className="space-y-4">
            <div className="flex justify-between items-center">
              <h2 className="font-bold flex items-center gap-2"><GraduationCap className="w-4 h-4" /> Education</h2>
              <Button onClick={addEducation} variant="ghost" size="sm">+ Add</Button>
            </div>
            {resume.education.map((edu: any, i: number) => (
              <div key={i} className="grid grid-cols-2 gap-2 border-l-2 pl-4 py-2">
                <Input placeholder="University" value={edu.school} onChange={(e) => updateEducation(i, 'school', e.target.value)} />
                <Input placeholder="Graduation Date" value={edu.date} onChange={(e) => updateEducation(i, 'date', e.target.value)} />
              </div>
            ))}
          </div>

          {/* 工作经历 */}
          <div className="space-y-4">
            <div className="flex justify-between items-center">
              <h2 className="font-bold flex items-center gap-2"><Briefcase className="w-4 h-4" /> Professional Experience</h2>
              <Button onClick={addExperience} variant="ghost" size="sm">+ Add</Button>
            </div>
            {resume.experience.map((exp: any, i: number) => (
              <Card key={i} className="shadow-none border-slate-200">
                <CardContent className="pt-4 space-y-3">
                  <Input placeholder="Company" value={exp.company} onChange={(e) => updateExperience(i, 'company', e.target.value)} />
                  <div className="flex justify-between items-center">
                    <Label className="text-xs">Role Description</Label>
                    <Button variant="ghost" size="sm" className="text-blue-600 h-6" onClick={() => handleOptimize(i, exp.description)}>
                      {loadingIndex === i ? <Loader2 className="w-3 h-3 animate-spin" /> : <Sparkles className="w-3 h-3 mr-1" />} AI Polish
                    </Button>
                  </div>
                  <textarea 
                    className="w-full p-3 text-sm border rounded-md h-28" 
                    value={exp.description}
                    onChange={(e) => updateExperience(i, 'description', e.target.value)}
                  />
                </CardContent>
              </Card>
            ))}
          </div>
        </div>
      </div>

      {/* 右侧预览区 (US Tech Style) */}
      <div className="w-1/2 bg-slate-500 p-12 overflow-y-auto flex justify-center">
        <div id="resume-preview" className="w-[210mm] min-h-[297mm] h-fit bg-white p-[20mm] shadow-2xl font-serif text-[#111] leading-normal">
          <div className="text-center border-b-2 border-black pb-4">
            <h1 className="text-3xl font-bold uppercase tracking-widest">{resume.personalInfo.fullName || "YOUR NAME"}</h1>
            <p className="text-sm mt-2">{resume.personalInfo.email} | {resume.personalInfo.phone || "Location"}</p>
          </div>

          <section className="mt-8">
            <h2 className="text-md font-bold border-b border-slate-300 uppercase mb-3">Education</h2>
            {resume.education.map((edu: any, i: number) => (
              <div key={i} className="flex justify-between mb-1">
                <span className="font-bold">{edu.school}</span>
                <span>{edu.date}</span>
              </div>
            ))}
          </section>

          <section className="mt-6">
            <h2 className="text-md font-bold border-b border-slate-300 uppercase mb-3">Work Experience</h2>
            {resume.experience.map((exp: any, i: number) => (
              <div key={i} className="mb-5 text-[10.5pt]">
                <div className="flex justify-between font-bold">
                  <span>{exp.company}</span>
                  <span className="italic">{exp.role}</span>
                </div>
                <div className="mt-2 text-slate-700 whitespace-pre-line leading-relaxed">
                  {exp.description}
                </div>
              </div>
            ))}
          </section>
        </div>
      </div>
    </div>
  );
}